@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i);
.app{position:absolute;top:0;left:0;bottom:0;right:0;display:grid;grid-template-columns:250px auto 300px;grid-template-rows:100%;grid-column-gap:3px;min-width:800px;height:100%;width:100%;background:#202020}.app .workspace{width:250px;height:100%}.app .settings{height:100%;overflow:auto}.app .renders{width:300px;height:100%;overflow-y:auto;overflow-x:hidden;scrollbar-width:none}.app .renders .view{width:300px;height:420px;background:white;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}

.react-contexify{position:fixed;opacity:0;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;box-sizing:border-box;padding:10px 10px;background-color:#1a1a1a;box-shadow:5px 5px 15px #00000066;border-radius:3px;cursor:pointer}.react-contexify .react-contexify__submenu{position:absolute;top:0;pointer-events:none;transition:opacity 0.275s;background-color:#282828fa}.react-contexify__submenu-arrow{font-size:12px;position:absolute;right:10px;line-height:22px}.react-contexify__separator{float:left;width:100%;height:1px;cursor:default;margin:4px 0;background-color:#666666}.react-contexify__item{font-size:14px;color:#b8b8b8;font-weight:bold;cursor:pointer;position:relative}.react-contexify__item.delete{color:#f04747}.react-contexify__item:not(.react-contexify__item--disabled):hover>.react-contexify__item__data{color:inherit;background-color:#313131}.react-contexify__item:not(.react-contexify__item--disabled):hover>.react-contexify__submenu{pointer-events:initial;opacity:1}.react-contexify__item--disabled{cursor:default;opacity:.5}.react-contexify__item__data{min-width:100px;padding:6px 12px;display:flex;text-align:left;white-space:nowrap;color:inherit;position:relative}.react-contexify__item__icon{font-size:20px;margin-right:8px;width:12px;text-align:center}

.empty{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:white}.editor{background:#313131;color:#cccccc;padding:15px 10px;min-height:calc(100% - 30px)}.editor .groupsettings{margin-bottom:20px}.editor .title{font-weight:bold;font-size:25px;border-bottom:solid 1px #cccccc;padding-bottom:3px;margin-bottom:10px}.editor label{font-weight:bold;display:block;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.editor label img{max-height:50px;display:block}.editor label:hover>input,.editor label:hover>textarea{border-bottom:solid 1px #777777}.editor label input,.editor label textarea{-webkit-user-select:contain;-moz-user-select:contain;-ms-user-select:element;user-select:contain;resize:none;font-weight:normal;background:#00000080;color:#cccccc;border:solid 1px transparent;padding:2px 5px;display:block;width:calc(100% - 20px)}.editor label input:focus,.editor label textarea:focus{outline:none;border-bottom:solid 1px #cccccc}.editor .save{font-weight:bold;display:block;margin-top:10px;border:none;background:#cccccc;color:#000000;padding:3px 10px;border-radius:5px}.editor .save.unsaved{background:#711111;color:#cccccc}.editor .save:hover{background:#315131;color:#cccccc}

.explorer{display:grid;grid-template-columns:auto;grid-template-rows:40px calc(100% - 60px);grid-column-gap:10px;background:#313131;color:#cccccc;height:100%;overflow:hidden}.explorer .head{grid-row:1;height:100%;padding:0px 50px;margin-left:-50px;width:100%}.explorer .head.scrolled{box-shadow:0px 10px 10px #000000}.explorer .head .search{display:inline-block;width:calc(100% - 60px);margin:8px 5px 8px 10px;border:1px solid transparent;padding:0px 3px;background:#555555}.explorer .head .search input{outline:none;height:20px;width:calc(100%);background:transparent;color:#cccccc;border:none;font-size:15px;padding:0px;display:inline-block}.explorer .head .search input::-ms-clear{display:none}.explorer .head .search input.short{width:calc(100% - 20px)}.explorer .head .search .x{height:20px;font-size:16px;width:20px;color:#cccccc;text-align:center;display:inline-block;position:relative;vertical-align:text-bottom;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.explorer .head .search .x:hover{color:#f04747}.explorer .head .search .x svg{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.explorer .head .add{color:#cccccc;text-align:center;display:inline-block;position:relative;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;width:20px;margin:0 5px}.explorer .head .add:hover{color:#43b581}.explorer .children{grid-row:2;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;overflow:auto;height:100%;padding:10px 0px 10px 0px}.explorer .children.dragover{background:#314131}.explorer .children .group .highlight{background:#ff440041}.explorer .children .group .title{padding:0px 0px 0px 10px}.explorer .children .group .title .caret{display:inline-block;width:20px;text-align:center}.explorer .children .group .title .name{display:inline-block;font-weight:bold;font-size:20px}.explorer .children .group .title:hover{background:#414141}.explorer .children .group.selected>.title{background:#2b5c3b}.explorer .children .group .cards .card{min-height:20px;padding:3px 0px 3px 30px}.explorer .children .group .cards .card:hover{background:#414141}.explorer .children .group .cards .card.selected{background:#515151}

.modal{display:grid;grid-template-columns:auto;grid-template-rows:auto 40px 30px;height:100%;grid-column-gap:10px;color:#cccccc;font-size:20px}.modal .dialog{grid-row:1;margin-left:10px}.modal .dialog .info .type{font-weight:bold}.modal .dialog .info .name{font-weight:bolder;font-style:italic}.modal .warn{grid-row:2;margin-left:10px;font-weight:bold}.modal .warn .irreversible{color:#f04747}.modal .buttons{grid-row:3;text-align:center}.modal .buttons button{background:#313131;border:none;color:#cccccc;padding:5px 10px;border-radius:5px;margin:0px 10px}.modal .buttons button.delete{color:#f04747;font-weight:bold}.modal .buttons button.delete:hover{background:#f04747;color:#FFFFFF}.modal .buttons button.good{color:#17831d;font-weight:bold}.modal .buttons button.good:hover{background:#17831d;color:#FFFFFF}.modal .buttons button:hover{background:#515151}

